$color-light: rgb(240, 240, 240);
$color-light-xs: rgb(250, 250, 250);
$color-light-s: rgb(232, 232, 241);
$color-light-blue: rgb(230, 240, 245);
$color-dark-s: rgb(187, 187, 187);
$color-dark-m: rgb(89, 89, 92);
$color-dark-l: rgb(47, 47, 71);
$color-dark-xl: rgb(19,18,43);
$color-brown: rgb(94, 81, 66);

$HEADER-CONTAINER-WIDTH: 90vw;
$HEADER-CONTAINER-HEIGHT: 70px;

$FONT-GILL: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
$FONT-MONOSPASE: monospace;

.logo {
  font-size: 1.5rem;
  color: $color-dark-l;
  text-decoration: none;
  font-family: $FONT-GILL;
  &:hover {
    color: $color-brown;
  }
}
.subtitle {
  font-family: $FONT-MONOSPASE;
  font-size: 0.9rem;
}
header {
  background: $color-light-s;
  background: linear-gradient(90deg, rgba(216,215,230,1) 0%, rgba(239,239,255,1) 35%, rgba(181,192,194,1) 100%);  
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
}
.header-container {
  width: $HEADER-CONTAINER-WIDTH;
  height: $HEADER-CONTAINER-HEIGHT;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
}
.cart {
  display: flex;
  align-items: center;
}
.bascet-logo {
  height: 2.5rem;
  width: 2.5rem;
  opacity: 70%;
}
.cart-counter {
  font-family: $FONT-MONOSPASE;
  font-size: 2rem;
  color: $color-dark-xl;
  background-color: $color-light;
  border-radius: 100%;
  height: 2rem;
  width: 2.5rem;
  text-align: center;
  padding-bottom: 10%;
  margin-left: -9px;
  z-index: 2;
  margin-top: 12px;
}
.search-container {
  margin-left: 25%;
  padding: 3px;
  background-color: rgba(19,20,21,.1);
  border: 1px solid $color-dark-m;
  border-radius: 20px;
  padding-left: 20px;
  position: relative;
  display: flex;
  justify-content: space-between;
  .search-input {
    background-color: transparent;
    border: 0;
    width: 100%;
    &:active, &:hover, &:focus {
      outline: 0;
      outline-offset: 0;
      cursor: pointer;
    }
    &::-webkit-search-cancel-button {
      -webkit-appearance: none;
      height: 15px;
      width: 15px;
      background: (url('../assets/icons/close-button.png') no-repeat);
      background-size: 15px 15px;
    }
  }
  .close-button {
    opacity: 0.8;
  }
}