.footer{height:7vh;width:100%;display:flex;align-items:center;justify-content:space-evenly;margin-top:20px;background:linear-gradient(90deg, rgba(73, 72, 74, 0.9) 0%, rgba(39, 39, 48, 0.9) 35%, rgba(73, 72, 74, 0.9) 90%)}.footer .footer-content{color:#f0f0f0;font-size:.8em}.footer .copyright{font-size:.8em;color:#f0f0f0;text-align:center}.footer .copyright:before{content:"©"}.footer .copyright a{color:#f0f0f0}.footer .copyright a:hover{color:#f0f0f0}.github-link{background-image:url("../assets/icons/Github-icon.png");cursor:pointer;height:31px;width:31px;background-repeat:no-repeat}.rss-link{background-image:url("../assets/icons/rs_school_img.svg");width:5em;height:2em;cursor:pointer;background-repeat:no-repeat}@media(max-width: 575px){.footer-content{display:none}}